import React, { Component } from "react";
import "../../styles/shoppingcart.css";
import "../../styles//my/makeorder.css";
import { basic_url } from "../../env";
export default class MakeOrder extends Component {
  constructor(props) {
    super(props);
    this.state = {
      qty: this.props.location.state.qty
    };
    this.increase = this.increase.bind(this);
    this.decrease = this.decrease.bind(this);
    this.submit = this.submit.bind(this);
  }
  render() {
    let info = this.props.location.state;
    let totalMoney =
      parseInt(this.state.qty) * parseInt(this.props.location.state.price);
    return (
      <div className="makeorder-page">
        <div className="buyer-info right-arrow">
          <p>
            <span></span>
            <span className="number">18518888888</span>
          </p>
          <p></p>
          <p className="primary-color"></p>
        </div>
        <div className="intro">
          <div className="imgbox">
            <img src={basic_url + info.imgurl} alt="" />
          </div>
          <div className="introtext">
            <p className="intro">{info.intro}</p>
            <p className="price">{info.price}</p>
          </div>
          <div className="num">
            <div className="choosenum">
              <span className="decrease" onClick={() => this.decrease()}>
                -
              </span>
              <span>{this.state.qty}</span>
              <span className="increase" onClick={() => this.increase()}>
                +
              </span>
            </div>
          </div>
        </div>
        <div className="makesure-info">
          <div className="right-arrow">
            <p></p>
            <p></p>
          </div>
          <div className="right-arrow">
            <p></p>
            <p></p>
          </div>
          <div className="right-arrow">
            <p></p>
            <p></p>
          </div>
          <div className="right-arrow">
            <p></p>
            <p></p>
          </div>
          <div>
            <p>
              1 
              <span className="primary-color"> {totalMoney}</span>{" "}
            </p>
          </div>
        </div>
        <div className="makesure-footer">
          <div>
            <span className="primary-color"> {totalMoney}</span>
          </div>
          <div onClick={this.submit}></div>
        </div>
      </div>
    );
  }
  submit() {
    let info = this.props.location.state;
    this.props.submit(
      info.id,
      info.imgurl,
      info.price,
      info.intro,
      this.state.qty
    );
    this.props.history.push("/my/allorder");
  }
  decrease() {
    this.setState({
      qty: Math.min(1, this.state.qty)
    });
  }
  increase() {
    this.setState({
      qty: this.state.qty + 1
    });
  }
}



